import { ActionCreator, ActionCreatorsMapObject, AsyncThunk, bindActionCreators } from "@reduxjs/toolkit";
import { useMemo } from "react";
import { useAppDispatch } from "common/hooks/useAppDispatch";

/**
 *   useActions  React-,    actions  -  (action creators)
 *       (bound actions),     .
 *   useActions   useMemo         bindActionCreators
 *   Redux      Redux.
 *  BoundActions      .       actions,
 *   -  ,   .        createAsyncThunk  Redux Toolkit,
 *       BoundAsyncThunk,   ,   
 *  Promise    .
 *  BoundAsyncThunk    ,    Redux.   ,
 *     ,   Promise    .
 *  ,   useActions        
 *    -     .
 */
export const useActions = <Actions extends ActionCreatorsMapObject = ActionCreatorsMapObject>(
  actions: Actions
): BoundActions<Actions> => {
  const dispatch = useAppDispatch();

  return useMemo(() => bindActionCreators(actions, dispatch), []);
};

// Types
type BoundActions<Actions extends ActionCreatorsMapObject> = {
  [key in keyof Actions]: Actions[key] extends AsyncThunk<any, any, any>
    ? BoundAsyncThunk<Actions[key]>
    : Actions[key];
};

type BoundAsyncThunk<Action extends ActionCreator<any>> = (
  ...args: Parameters<Action>
) => ReturnType<ReturnType<Action>>;
